@import url('./_reset.scss');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Times New Roman", sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

.center {
  text-align: center;
}

.btn {
  background-color: rgb(201, 201, 201);
  padding: 10px;
  border: none;
  outline: none;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.479);
  transition: all .5s ease;
  &:hover {
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.479);
    transform: translateY(-5px);
  }
}
